@import url(https://fonts.googleapis.com/css?family=Courier+Prime&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*overflow: hidden;*/
}

body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
	font-family: 'Courier Prime', monospace;
}

.container {
  position: absolute;
  width: 100%;
  height: 757px;
  background: #abbaab;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#ffffff), to(#abbaab));
  background: linear-gradient(to right, #ffffff, #abbaab); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.Instructions {
  margin: auto;
  padding: 20px 0;
  text-align: center;
  font-weight: bold;
  font-size: 2em;
  text-shadow: 2px 2px #4169E1;
  border-style: dashed;
}

.range {
	margin: auto;
	padding: 20px 0;
	display: grid;
	grid-column: 1fr 1fr;
	justify-content: center;
}

.min-div, .max-div {
	margin: 5px;
	display: flex;
	justify-content: center;
}

.range h2 {
	display: flex;
	justify-content: center;
	text-shadow: 2px 2px #4169E1;
}

#minInput,#maxInput {
	margin: 15px;
	font-family: 'Courier Prime', monospace;
	font-weight: bold;
	text-align: center;
	box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}

.gen-box {
	display: flex;
	justify-content: center;
	margin: auto;
	padding: 20px 0;
	
}

.gen-btn {
	padding: 20px 40px;
	font-size: 40px;
	-webkit-transition-duration: 0.4s; /* Safari */
  	transition-duration: 0.4s;
	border-radius: 12px;
}

.gen-btn:hover {
  background-color: #008CBA;
  color: white;
}

.gen-btn:focus {
	outline-width: 0;
}

.view {
	display: flex;
	justify-content: center;
	margin: 20px auto;
	padding: auto 0;
}

.view h1 {
	font-size: 4em;
	margin: 0;
	border-style: groove;
	border-color: #4169E1;
	text-shadow: 2px 2px #4169E1;
}
